"use client";
import { Dispatch, SetStateAction } from "react";
import Link from "next/link";
import { X } from "lucide-react";
import { useCart } from "@/lib/CartContext"; 

interface Game {
  id: string;
  title: string;
  image: string;
  price: number;
}

export default function CartSidebar({
  isOpen,
  setIsOpen,
  cart,
  setCart,
}: {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  cart: Game[];
  setCart: Dispatch<SetStateAction<Game[]>>;
}) {
  const removeFromCart = (id: string) => {
    const newCart = cart.filter((g) => g.id !== id);
    setCart(newCart);
    localStorage.setItem("cart", JSON.stringify(newCart));
  };

  const total = cart.reduce((sum, g) => sum + g.price, 0).toFixed(2);

  return (
    <div
      className={`fixed top-0 right-0 h-full w-80 bg-gray-900 shadow-lg transform transition-transform z-50 ${
        isOpen ? "translate-x-0" : "translate-x-full"
      }`}
    >
      <div className="flex justify-between items-center p-4 border-b border-gray-700">
        <h2 className="text-lg font-bold">Carrinho</h2>
        <button onClick={() => setIsOpen(false)}>
          <X className="w-5 h-5 text-white" />
        </button>
      </div>

      <div className="p-4 flex flex-col gap-4 overflow-y-auto h-[calc(100%-120px)]">
        {cart.length === 0 ? (
          <p className="text-gray-300">Seu carrinho est√° vazio.</p>
        ) : (
          cart.map((game) => (
            <div key={game.id} className="flex items-center gap-3 bg-gray-800 p-2 rounded-lg">
              <img src={game.image} alt={game.title} className="w-16 h-16 object-cover rounded" />
              <div className="flex-1">
                <p className="text-white font-semibold">{game.title}</p>
                <p className="text-gray-400">‚Ç¨{game.price.toFixed(2)}</p>
              </div>
              <button
                onClick={() => removeFromCart(game.id)}
                className="text-red-500 hover:text-red-400"
              >
                Remover
              </button>
            </div>
          ))
        )}
      </div>

      {cart.length > 0 && (
        <div className="p-4 border-t border-gray-700">
          <p className="text-white font-bold mb-2">Total: ‚Ç¨{total}</p>
          <Link
  href="/Checkout"
  className="block w-full text-center bg-blue-600 hover:bg-blue-700 py-2 rounded-lg font-semibold"
  onClick={() => {
    console.log("üõí Finalizando compra. Jogos no carrinho:", cart);
    setIsOpen(false);
  }}
>
  Finalizar Compra
</Link>
        </div>
      )}
    </div>
  );
}
