"use client";

import { createContext, useContext, useState, useEffect, ReactNode } from "react";
import { supabase } from "@/lib/supabaseClient";

interface User {
  id?: string; // UUID do Supabase
  name: string;
  email: string;
  balance?: number;
}

interface AuthContextType {
  id: string | undefined;
  user: User | null;
  login: (userData: User) => void;
  logout: () => void;
  addFunds: (amount: number) => void;
}

const AuthContext = createContext<AuthContextType>({
  user: null,
  login: () => {},
  logout: () => {},
  addFunds: () => {},
});

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);

  useEffect(() => {
    const savedUser = localStorage.getItem("user");
    console.log("üîπ Tentando restaurar sess√£o...");
    if (savedUser) {
      const parsed = JSON.parse(savedUser);
      console.log("üîπ Sess√£o restaurada:", parsed);
      setUser(parsed);
    }
  }, []);

  const login = (userData: User) => {
    console.log("‚úÖ Login chamado com dados:", userData);
    setUser(userData);
    localStorage.setItem("user", JSON.stringify(userData));
  };

  const logout = () => {
    console.log("üîπ Logout chamado");
    setUser(null);
    localStorage.removeItem("user");
  };

  const addFunds = async (amount: number) => {
    console.log("üí∞ Tentando adicionar fundos:", amount);
    console.log("üßæ Usu√°rio atual:", user);

    if (!user) {
      console.error("‚ùå Usu√°rio n√£o definido!");
      return alert("Usu√°rio n√£o definido.");
    }

    if (!user.id) {
      console.error("‚ùå Usu√°rio n√£o tem ID definido! N√£o √© poss√≠vel atualizar no Supabase.");
      return alert("Usu√°rio sem ID. Login incorreto ou n√£o sincronizado com o Supabase.");
    }

    const newBalance = (user.balance || 0) + amount;

    try {
      const { error } = await supabase
        .from("users")
        .update({ balance: newBalance })
        .eq("id", user.id);

      if (error) throw error;

      const updatedUser = { ...user, balance: newBalance };
      setUser(updatedUser);
      localStorage.setItem("user", JSON.stringify(updatedUser));
      console.log(`üíµ Adicionados ‚Ç¨${amount.toFixed(2)}. Novo saldo:`, newBalance);
    } catch (err) {
      console.error("‚ùå Erro ao atualizar saldo:", err);
      alert("Erro ao atualizar saldo no Supabase.");
    }
  };

  return (
    <AuthContext.Provider value={{ user, login, logout, addFunds }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);
